import RenderTask from './worker/RenderTask'

export default function app(ctx: CanvasRenderingContext2D, width: number, height: number) {

    //:ImageData
    const imageData = ctx.createImageData(width, height)
    //: Uint8ClampedArray
    const image = imageData.data



    const onmessage: { [key: string]: Function } = {
        warning(msg: string) {
            alert(msg)
        },
        reportProgress: (bar => (status: string, num: number) => {
            if (num)
                bar.style.width = num * 100 + '%'
            if (status == 'complete')
                bar.style.width = '100%'
        })(document.getElementById('processline')),
        render(image: number[]) {

            image.forEach((v, i) => { imageData.data[i] = v })

            ctx.putImageData(imageData, 0, 0)
        },
        partComplete(task: RenderTask) {
            const
                image = task.pixels.map(v => v.color).reduce((res: number[], v: number[]) => res.concat(v)
                    , []),
                position = task.position


            image.forEach((v, i) => { imageData.data[i + position] = v })

            ctx.putImageData(imageData, 0, 0)
        }
    }


    const worker = new Worker('/dist/main.worker.js')


    // worker.postMessage({
    //     method: 'render',
    //     args: [image, width, height]
    // })


    worker.postMessage({
        method: 'render',
        args: [width, height]
    })

    worker.onmessage = function ({ data }) {
        const { method, args } = data

        if (onmessage[method])
            onmessage[method](...args)
        else
            alert(`app: can't find method(${method})`)
    }

}